if (document.querySelector('.useful__container')) {
  const showMore = document.querySelector('.show-more');
  const productsLength = document.querySelectorAll('.useful__wrapper-item').length;
  // const activeItem = document.querySelectorAll('.useful__item');
  let items = 14;
  showMore.addEventListener('click', () => {
    items += 3;
    const array = Array.from(document.querySelector('.useful__wrapper').children);
    const visItems = array.slice(0, items);
    visItems.forEach(el => el.classList.add('is-visible'));
    if (visItems.length === productsLength) {
      showMore.style.display = 'none';
    }
  });
  const activeLists = document.querySelectorAll('.useful__list');
  activeLists.forEach(item => {
    const activeItem = item.querySelector('.useful__item');
    activeItem.addEventListener('click', () => {
      if (item.classList.contains('active')) {
        item.classList.remove('active');
      } else {
        item.classList.add('active');
      }
    });
  });
}
